@mixin placeholder-color ($color, $focus) {
    &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
      color: $color;
      // font-size: 15px;

      &:focus{
        &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
          color: $focus;
          // font-size: 15px;

        }
      }
    }
    &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
     color: $color;
     opacity:  1;
    //  font-size: 15px;
     &:focus{
       &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
          color: $focus;
          opacity:  1;
          // font-size: 15px;

       }
     }
    }
    &::-moz-placeholder { /* Mozilla Firefox 19+ */
     color: $color;
     opacity:  1;
    //  font-size: 15px;

     &:focus{
       &::-moz-placeholder { /* Mozilla Firefox 19+ */
          color: $focus;
          opacity:  1;
          // font-size: 15px;

       }
     }
    }
    &:-ms-input-placeholder { /* Internet Explorer 10-11 */
     color: $color;
    //  font-size: 15px;

     &:focus{
       &:-ms-input-placeholder { /* Internet Explorer 10-11 */
          color: $focus;
          // font-size: 15px;

       }
     }
   }
  }
  .input_text{  
  @include placeholder-color(#C0C0C0, #C0C0C0);
  text-align: start;
  }


  @media screen and (min-width: 601px) {
